import Vue from 'vue'
import Router from 'vue-router' //引进路由


import Login from './components/Login.vue'
import Regist from './components/Regist.vue'

Vue.use(Router)

const router = new Router({
	// mode: 'history',
	base: process.env.BASE_URL,
	routes: [
		{path: '/login',component:Login},
		{path: '/regist',component:Regist},
		{path: '/xxx',component:Regist},
		{path: '/regist',component:Regist,alias:'zhuce'}
	]
})


router.beforeEach((to,from,next)=>{
	// console.log(`路由是从${from.path}跳转到${to.path}的页面`);
	// alter(`路由是从${from.path}跳转到${to.path}的页面`);
	if(to.path=='/zhuce'){
		alert("您现在还没有权利去注册，请返回，谢谢~~");
	}else{
		next();
	}
})

export default router;
